import { useParams } from "react-router";
import Button from "react-bootstrap/Button";
// import { Badge } from "react-bootstrap";
import { useState, useEffect, useRef } from "react";
import axios from "axios";
import Compress from "browser-image-compression";
import Loader from "react-loader-spinner";
import { CKEditor } from "@ckeditor/ckeditor5-react";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import { Link } from "react-router-dom";
import Alert from "react-bootstrap/Alert";
import { useDispatch } from "react-redux";
import { resetState } from "./redux";

const MAX_LENGTH = 1;

const config = {
  toolbar: {
    items: [
      "heading",
      "|",
      "bold",
      "italic",
      "link",
      "outdent",
      "indent",
      "bulletedList",
      "numberedList",
      "insertTable",
      "blockQuote",
      "undo",
      "redo",
    ],
  },
  link: {
    // Automatically add target="_blank" and rel="noopener noreferrer" to all external links.
    addTargetToExternalLinks: true,
  },
};

const EditBlog = () => {
  const { id } = useParams();
  // eslint-disable-next-line
  // const history = useHistory();
  const [title, setTitle] = useState("");
  const [body, setBody] = useState("");
  const [isPending, setIsPending] = useState(false);
  const [reload, setReload] = useState(false);
  const [imageModified, setImageModified] = useState(false);
  const [selectedFile, setSelectedFile] = useState([]);
  const [imaj, setImaj] = useState([]);
  const [isFilePicked, setIsFilePicked] = useState(false);
  const [images, setImages] = useState([]);
  // eslint-disable-next-line
  const [blog, setBlog] = useState(null);
  const [error, setError] = useState(null);
  const [errorUI, setErrorUI] = useState(null);
  const [wordCount, setWordCount] = useState(0);
  // eslint-disable-next-line
  const [pending, setPending] = useState(null);
  const unmounted = useRef(null);
  const dispatch = useDispatch();

  useEffect(() => {
    unmounted.current = false;
    setPending(true);
    setReload(false);
    let config = {
      headers: {
        "Content-Type": "application/x-www-form-urlencoded",
        Accept: "application/json",
      },
      withCredentials: true,
      crossDomain: true,
    };
    axios
      .get("/api/blogs/" + id, config)
      .then((res) => {
        // console.log("res ", res.data);
        setBlog(res.data[0].blog);
        setTitle(res.data[0].blog.title);
        setBody(res.data[0].blog.body);
        setImages([]);
        setImages((images) => images.concat(res.data[0].blog.imageId));
        // setAuthor(res.data[0].author);
        setPending(false);
      })
      .catch((err) => {
        console.log(err.message);
        setPending(false);
        setError(err);
      });
    return () => {
      setPending(false);
      unmounted.current = true;
      dispatch(resetState());
    };
  }, [id, reload, dispatch]);

  const changeHandler = (event) => {
    setIsFilePicked(true);
    setPending(false);
    setErrorUI(null);
    const options = {
      // As the key specify the maximum size
      // Leave blank for infinity
      maxSizeMB: 1,
      // Use webworker for faster compression with
      // the help of threads
      useWebWorker: true,
    };

    if (Array.from(event.target.files).length > MAX_LENGTH) {
      event.preventDefault();
      setErrorUI(
        `bir kerede ${MAX_LENGTH} resim yükleyebilirsiniz. resim yüklerken eğer veritabınında 4 resminiz varsa en eskisi (soldan ilk resim) silinecek`
      );
      event.target.value = null;
      setIsFilePicked(false);
      return;
    }

    // setSelectedFile((selectedFile) => [...selectedFile, event.target.files]);
    setSelectedFile(event.target.files);

    setImaj([]);
    for (const file of event.target.files) {
      Compress(file, options)
        .then((compressedBlob) => {
          compressedBlob.lastModifiedDate = new Date();

          const convertedBlobFile = new File([compressedBlob], file.name, {
            type: file.type,
            lastModified: Date.now(),
          });
          let r = URL.createObjectURL(convertedBlobFile);
          console.log(r);

          setImaj((imaj) => imaj.concat(r));
          // one.src = URL.createObjectURL(event.target.files);
          // one.onload = function () {
          //   URL.revokeObjectURL(one.src); // free memory
          // };
          // };
          setImageModified(true);
        })
        .catch((e) => {
          // Show the user a toast message or notification that something went wrong while compressing file
          console.log(e.message);
        });
    }
  };

  const handleImageDelete = (e, blogId, imageId, body, title) => {
    e.preventDefault();
    // var formData = new FormData();
    // formData.append("blog", body);
    // formData.append("title", title);

    // for (let [key, value] of formData.entries()) {
    //   console.log(key, value);
    // }

    let config = {
      headers: {
        // "Content-Type": "application/x-www-form-urlencoded",
        Accept: "application/json",
        blogId: blogId,
      },
      withCredentials: true,
      crossDomain: true,
    };

    let data = {
      blog: body,
      title: title,
    };

    axios
      .patch(`/api/image/${imageId}`, data, config)
      .then(() => setReload(true))
      .catch((error) => console.log(error.message));
  };

  const handleClickForEdit = (e) => {
    e.preventDefault();
    setIsPending(true);

    let formData;
    let config;

    // console.log("imageModified ", imageModified);

    if (imageModified) {
      formData = new FormData();

      for (const file of selectedFile) {
        formData.append("files", file);
      }
      //formData.append("files", selectedFile); //this is wrong. the right way is the for loop above.
      formData.append("body", body);
      formData.append("title", title);
      config = {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded",
          Accept: "application/json",
        },
        withCredentials: true,
        crossDomain: true,
      };
    } else {
      config = {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
        },
        withCredentials: true,
        crossDomain: true,
      };

      formData = {
        body: body,
        title: title,
      };
    }

    axios
      .put(`/api/blogs/${id}/edit`, formData, config)
      .then((result) => {
        // if (result.data.no_image_action) history.push("/");
        if (result.data.too_many_pictures_at_once)
          setErrorUI("bir kerede bir resim yükleyebilirsiniz");
        if (result.data.title_too_short_or_long) {
          setErrorUI("başlık 1 karakter ile 100 karakter arasında olmalı");
          return;
        }
        if (result.data.body_too_short_or_long) {
          setErrorUI("blog 1 karakter ile 10000 karakter arasında olmalı");
          return;
        } else {
          setErrorUI(null);
          setImaj([]);
          setIsPending(false);
          setReload(true);
          // history.push(`/blogs/${id}/edit`);
        }
      })
      .catch((error) => {
        console.error("Error:", error);
      });
  };

  return (
    <div className="create">
      <h2>blog düzelt</h2>
      {errorUI && (
        <Alert data-testid="alertEditBlog" id="alertEditBlog" variant="danger">
          {errorUI}
        </Alert>
      )}
      <Link className="float-right" to="/">
        blog listesine git
      </Link>
      <p></p>
      <form action="">
        <label htmlFor="">blog başlığı:</label>
        <input
          type="text"
          required
          value={title}
          data-testid="titleInput"
          id="titleInput"
          onChange={(e) => {
            setErrorUI(null);
            setIsPending(false);
            setTitle(e.target.value);
            setImageModified(false);
          }}
        />

        <label htmlFor="">blog:</label>
        <div className="App">
          <CKEditor
            editor={ClassicEditor}
            data={body}
            config={config}
            onReady={(editor) => {
              // You can store the "editor" and use when it is needed.
              // console.log("Editor is ready to use!", editor);
            }}
            onChange={(event, editor) => {
              const data = editor.getData();
              setBody(data);
              // console.log({ event, editor, data });
              setWordCount(data.length);
              setErrorUI(null);
              setIsPending(false);
              setImageModified(false);
            }}
            onBlur={(event, editor) => {
              // console.log("Blur.", editor);
            }}
            onFocus={(event, editor) => {
              // console.log("Focus.", editor);
            }}
          />
          <div id="word-count">
            {10000 - wordCount} karater yazma hakkınız kaldı
          </div>
        </div>
        <p></p>
        <Alert variant="info">
          bu ekranda bir kerede 1 resim yükleyebilirsiniz
          <br />
          resim yüklerken eğer veritabınında 4 resminiz varsa en eskisi <br />
          (soldan ilk resim) silinecektir
        </Alert>
        <p></p>
        {images.map((image, index) => {
          return (
            <span key={index} id={image._id}>
              <Button
                variant="danger"
                id="eraseButton"
                data-testid="eraseButton"
                onClick={(e) =>
                  handleImageDelete(e, id, image._id, body, title)
                }
              >
                sil
              </Button>
              <img
                alt="blog"
                src={`data:image/jpeg;base64,${image["data"]}`}
                height="75px"
                width="75px"
                className="images"
                data-testid="imgTag"
              />
            </span>
          );
        })}
        <p></p>
        <input
          type="file"
          name="files"
          id="files"
          onClick={(e) => (e.target.value = null)}
          onChange={changeHandler}
        />
        <p></p>
        {imageModified &&
          imaj &&
          imaj.map((im, index) => (
            <img
              id="one"
              src={im}
              key={index}
              alt="upload"
              width="100"
              height="100"
              className="images"
            />
          ))}
        {isFilePicked && (
          <Loader
            type="Puff"
            color="#00BFFF"
            height={100}
            width={100}
            timeout={1000} //3 secs
          />
        )}
        <p></p>
        {error && <Alert variant="danger">{error}</Alert>}
        <p></p>
        {errorUI && <Alert variant="danger">{errorUI}</Alert>}
        {(!isPending || errorUI) && (
          <button
            data-testid="blogEditButton"
            id="blogEditButton"
            onClick={handleClickForEdit}
          >
            blog büzelt
          </button>
        )}
        {isPending && !errorUI && <button>blog düzeltiliyor...</button>}
        <p></p>
        <Link className="float-right" to="/">
          blog listesine git
        </Link>
      </form>
    </div>
  );
};

export default EditBlog;
