import { useState, useEffect, useRef } from "react";
import { useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Alert from "react-bootstrap/Alert";
import { postForgotPassword } from "./redux";
// import { resetState } from "./redux";

const ForgotPassword = () => {
  const [email, setEmail] = useState("");
  const [isPending, setIsPending] = useState(false);
  const history = useHistory();
  const [postError, setPostError] = useState("");
  const error = useSelector((state) => state.auth?.error);
  const logged = useSelector((state) => state.auth?.logged);
  const message = useSelector((state) => state.auth?.message);
  const dispatch = useDispatch();
  let btnRef = useRef(true);

  useEffect(() => {
    try {
      setIsPending(false);
      if (logged) history.push("/");
    } catch (error) {
      console.log("error.message ", error.message);
    }
  }, [logged, history, isPending, dispatch]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsPending(true);

    try {
      dispatch(postForgotPassword(email));
      if (btnRef.current) {
        btnRef.current.setAttribute("disabled", "disabled");
      }
      return;
    } catch (error) {
      setPostError(error.message);
      console.log(error.message);
      btnRef.current = false;
      setIsPending(false);
    }
  };

  return (
    <Form onSubmit={handleSubmit}>
      {message && <Alert variant="primary">{message}</Alert>}
      {error && !message && <Alert variant="danger">{error}</Alert>}
      {postError && !message && <Alert variant="danger">{postError}</Alert>}
      <Form.Group controlId="formBasicEmail">
        <Form.Label>email</Form.Label>
        <Form.Control
          type="email"
          placeholder="email"
          data-testid="email"
          required
          value={email}
          onChange={(e) => setEmail(e.target.value)}
        />
        <Form.Text className="text-muted"></Form.Text>
      </Form.Group>
      <p></p>
      {!isPending && (
        <Button
          variant="primary"
          id="forgotPasswordButton"
          data-testid="forgotPasswordButton"
          ref={btnRef}
          type="submit"
        >
          email adresime şifre sıfırlama gönder
        </Button>
      )}
      {isPending && (
        <Button id="forgotPasswordButton" variant="primary">
          lütfen bekleyin...
        </Button>
      )}
    </Form>
  );
};

export default ForgotPassword;
