import { Link } from "react-router-dom";
import { CKEditor } from "@ckeditor/ckeditor5-react";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import Alert from "react-bootstrap/Alert";
import { /*connect */ useSelector } from "react-redux";

const BlogList = ({ data: blogs, title }) => {
  // console.log("blogs.blog.blogAuthorId ", blogs.docs[0].blogAuthorId);

  const errorFromServer = useSelector((state) => state.auth?.error);

  const config = {
    toolbar: {
      items: [],
      viewportTopOffset: 0,
    },
  };

  const authors = (authors) => {
    return Object.values(authors.map((author) => author.userName).join(", "));
  };

  return (
    <div>
      {errorFromServer && <Alert variant="danger">{errorFromServer}</Alert>}
      <h2> {title} </h2>
      {blogs && blogs.docs && blogs.docs.length === 0 ? (
        <p>Henüz hiç blog yayınlanmamış</p>
      ) : (
        blogs.docs?.map((blog) => {
          return (
            <div className="blog-preview" key={blog._id}>
              <Link data-testid="linkToBlog" to={`/blogs/${blog._id}`}>
                <h2>{blog.title.substring(0, 50)}</h2>
                <div className="App">
                  <CKEditor
                    config={config}
                    editor={ClassicEditor}
                    data={blog.body.substring(0, 50)}
                    onReady={(editor) => {
                      // You can store the "editor" and use when it is needed.
                      // console.log("Editor is ready to use!", editor);
                      if (editor) editor.isReadOnly = true;
                    }}
                    // onChange={(event, editor) => {
                    //   const data = editor.getData();
                    //   console.log({ event, editor, data });
                    // }}
                    onBlur={(event, editor) => {
                      // console.log("Blur.", editor);
                    }}
                    onFocus={(event, editor) => {
                      // console.log("Focus.", editor);
                    }}
                  />
                </div>
                <p>yazan: {authors(blog.blogAuthorId)}</p>
                <p className="forCenteringImages">
                  {blog.imageId &&
                    blog.imageId.map((image) => {
                      return (
                        <img
                          alt="blog"
                          src={`data:image/jpeg;base64,${image["data"]}`}
                          height="100px"
                          width="100px"
                          key={image._id}
                          className="images"
                        />
                      );
                    })}
                </p>
              </Link>
            </div>
          );
        })
      )}
    </div>
  );
};

export default BlogList;
