import { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Alert from "react-bootstrap/Alert";
import { changePassword } from "./redux";
// import { resetState } from "./redux";

const ChangePassword = () => {
  const [isPending, setIsPending] = useState(false);
  const [password, setPassword] = useState("");
  const [passwordConfirm, setPasswordConfirm] = useState("");
  const history = useHistory();
  const error = useSelector((state) => state.auth?.error);
  const message = useSelector((state) => state.auth?.message);
  const dispatch = useDispatch();

  useEffect(() => {
    try {
      setIsPending(false);
      // console.log("error ", error);
      if (message.length > 0 && error === "") history.push("/");
    } catch (error) {
      console.log("error.message ", error.message);
    }
    return () => {
      setIsPending(false);
      // dispatch(resetState());
    };
  }, [error, history, message.length, isPending, dispatch]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsPending(true);

    try {
      dispatch(changePassword(password, passwordConfirm));
      return;
    } catch (error) {
      console.log(error.message);
      setIsPending(false);
    }
  };

  return (
    <Form onSubmit={handleSubmit}>
      {error && <Alert variant="danger">{error}</Alert>}
      <Form.Group>
        <Form.Label>şifre</Form.Label>
        <Form.Control
          type="password"
          placeholder="şifre"
          value={password}
          data-testid="password"
          required
          onChange={(e) => setPassword(e.target.value)}
        />
      </Form.Group>
      <p></p>

      <Form.Group>
        <Form.Label>şifre tekrar</Form.Label>
        <Form.Control
          type="password"
          placeholder="tekrar aynı şifre"
          value={passwordConfirm}
          data-testid="passwordConfirm"
          required
          onChange={(e) => setPasswordConfirm(e.target.value)}
        />
      </Form.Group>
      <p></p>

      {!isPending && (
        <Button
          variant="primary"
          type="submit"
          data-testid="changePasswordButton"
        >
          gönder
        </Button>
      )}
      {isPending && <Button variant="primary">lütfen bekleyin...</Button>}
    </Form>
  );
};

export default ChangePassword;
