export const changePassword = (password, passwordConfirm) => {
  let config = {
    headers: {
      "Content-Type": "application/json",
      Accept: "application/json",
    },
    withCredentials: true,
    crossDomain: true,
  };
  return (dispatch) => {
    dispatch(changePasswordRequest());
    axios
      .post(
        "/api/changePassword",
        { password: password, passwordConfirm: passwordConfirm },
        config
      )
      .then(function (response) {
        const back = response.data;
        dispatch(changePasswordSuccess(back));
      })
      .catch(function (error) {
        console.log("error ", error);
        if (error.response.status === 411) {
          dispatch(
            changePasswordFailure(
              `şifre ${PASSWORD_LENGTH_MIN} harften az veya ${PASSWORD_LENGTH_MAX} harften fazla olamaz`
            )
          );
        } else if (error.response.status === 403) {
          dispatch(
            changePasswordFailure(
              "şifre ve tekrar şifre kutuları aynı değer olmalı"
            )
          );
        } else if (error.response.status === 500) {
          dispatch(
            changePasswordFailure(
              "teknik bir problem oldu. lütfen sonra tekrar deneyin."
            )
          );
        } else {
          const errorMsg = error.message;
          dispatch(changePasswordFailure(errorMsg));
        }
      });
  };
};

