export const postUser = (email, password) => {
  return (dispatch) => {
    dispatch(postUserRequest());
    axios
      .post(
        "/api/sign_in",
        {
          email: email,
          password: password,
        },
        { withCredentials: true, crossDomain: true }
      )
      .then(function (response) {
        // console.log("response ", response);
        const back = response.data;
        dispatch(postUserSuccess(back));
      })
      .catch(function (error) {
        console.log(error);
        if (error.response?.status === 422) {
          dispatch(postUserFailure("lütfen hesabınızı teyit edin"));
        } else if (error.response?.status === 401) {
          dispatch(postUserFailure("email sistemde kayıtlı değil"));
        } else if (error.response?.status === 403) {
          dispatch(postUserFailure("email veya şifre hatalı"));
        } else {
          const errorMsg = error.message;
          dispatch(postUserFailure(errorMsg));
        }
      });
  };
};
export const resetPassword = ({ password, passwordConfirm, token }) => {
  return (dispatch) => {
    dispatch(resetPasswordRequest());
    axios
      .post(`/api/resetPassword/${token}`, {
        password: password,
        passwordConfirm: passwordConfirm,
      })
      .then(function (response) {
        // console.log("response ", response);
        const back = response.data;
        dispatch(resetPasswordSuccess(back));
      })
      .catch(function (error) {
        if (error.response.status === 401) {
          dispatch(
            resetPasswordFailure(
              "gönderdiğimiz şifre sıfırlama emailinin süresi dolmuştur. lütfen tekrar isteyin",
              401
            )
          );
        } else if (error.response.status === 403) {
          dispatch(
            resetPasswordFailure("şifre ve onay şifresi aynı değil", 403)
          );
        } else if (error.response.status === 422) {
          dispatch(
            resetPasswordFailure(
              `şifre ${USERNAME_LENGTH_MAX} karakterden kısa veya ${PASSWORD_LENGTH_MAX} karakterden uzun olamaz`,
              422
            )
          );
        } else if (error.response.status === 500) {
          dispatch(resetPasswordFailure("teknik bir problem oldu", 500));
        } else {
          const errorMsg = error.message;
          dispatch(resetPasswordFailure(errorMsg));
        }
      });
  };
};

export const signup = ({ email, password, passwordConfirm, userName }) => {
  return (dispatch) => {
    dispatch(signupRequest());
    axios
      .post("/api/sign_up", {
        email: email,
        password: password,
        passwordConfirm: passwordConfirm,
        userName: userName,
      })
      .then(function (response) {
        const back = response.data;
        dispatch(signupSuccess(back));
      })
      .catch(function (error) {
        if (error.response.status === 409) {
          dispatch(signupFailure("email sistemde kayıtlı"));
        } else if (error.response.status === 400) {
          dispatch(signupFailure("kullanıcı adı alınmış"));
        } else if (error.response.status === 411) {
          dispatch(
            signupFailure(
              `kullanıcı adı ${USERNAME_LENGTH_MIN} harften az veya ${USERNAME_LENGTH_MAX} harften fazla olamaz veya emailiniz geçerli bir email değil`
            )
          );
        } else if (error.response.status === 403) {
          dispatch(signupFailure("şifre ve onay şifresi aynı değil"));
        } else if (error.response.status === 422) {
          dispatch(
            signupFailure(
              `şifre ${PASSWORD_LENGTH_MIN} karakterden kısa veya ${PASSWORD_LENGTH_MAX} karakterden uzun olamaz`
            )
          );
        } else if (error.response.status === 500) {
          dispatch(signupFailure("teknik bir problem oldu"));
        } else {
          const errorMsg = error.message;
          dispatch(signupFailure(errorMsg));
        }
      });
  };
};

export const checkAuth = () => {
  return (dispatch) => {
    dispatch(postAuthRequest());
    let url = "/api/protected";
    const options = {
      method: "GET",
      headers: {
        "Content-Type": "application/x-www-form-urlencoded",
        Accept: "application/json",
      },
      data: "",
      url,
      withCredentials: true,
      crossDomain: true,
    };
    axios(options)
      .then((response) => {
        const user = response.data;
        // console.log("user ", user);
        dispatch(postAuthSuccess(user));
      })
      .catch(function (error) {
        const errorMsg = error.message;
        console.log("error.message ", error.message);
        dispatch(postAuthFailure(errorMsg));
      });
  };
};

export const signout = () => {
  let url = "/api/sign_out";
  const options = {
    method: "GET",
    data: "",
    url,
    withCredentials: true,
    crossDomain: true,
  };
  return (dispatch) => {
    dispatch(postSignoutRequest());
    // console.log("signout");
    axios(options)
      .then((response) => {
        const received = response.data;
        dispatch(postSignoutSuccess(received));
      })
      .catch(function (error) {
        const errorMsg = error.message;
        dispatch(postSignoutFailure(errorMsg));
      });
  };
};

export const deleteBlog = (id) => {
  let config = {
    headers: {
      "Content-Type": "application/x-www-form-urlencoded",
      Accept: "application/json",
    },
    withCredentials: true,
    crossDomain: true,
  };
  return (dispatch) => {
    dispatch(deleteRequest());
    axios
      .delete(`/api/blogs/${id}`, config)
      .then((response) => {
        if (response.status === 204) {
          dispatch(deleteSuccess("blog silindi"));
        }
      })
      .catch((error) => {
        dispatch(
          deleteFailure(
            "silme isteği başariz oldu. lütfen site yöneticisine email atın"
          )
        );
      });
  };
};
