import React from "react";
import { Route } from "react-router-dom";
// import Home from "./Home";
// import Signin from "./Signin";

export const PublicRoute = ({
  isloading,
  isAuthenticated,
  component: Component,
  ...rest
}) => {
  return (
    !isloading && (
      <Route {...rest} component={(props) => <Component {...props} />} />
    )
  );
};

export default PublicRoute;
