import { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Alert from "react-bootstrap/Alert";
import { changeUsername } from "./redux";

const ChangeUsername = () => {
  const [isPending, setIsPending] = useState(false);
  const [userName, setUserName] = useState("");
  const [userNameConfirm, setUserNameConfirm] = useState("");
  const history = useHistory();
  const error = useSelector((state) => state.auth?.error);
  const message = useSelector((state) => state.auth?.message);
  const dispatch = useDispatch();

  // useEffect(() => {
  //   dispatch(resetState());
  // }, []);

  useEffect(() => {
    try {
      setIsPending(false);

      // console.log("error ", error);
      if (message.length > 0 && error === "") history.push("/");
    } catch (error) {
      console.log("error.message ", error.message);
    }
    return () => {
      setIsPending(false);
    };
  }, [error, history, message.length, isPending, dispatch]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsPending(true);

    try {
      dispatch(changeUsername(userName, userNameConfirm));
      return;
    } catch (error) {
      console.log(error.message);
      setIsPending(false);
    }
  };

  return (
    <Form onSubmit={handleSubmit}>
      {error && <Alert variant="danger">{error}</Alert>}
      <Form.Group>
        <Form.Label>kullanıcı adı</Form.Label>
        <Form.Control
          type="text"
          placeholder="kullanıcı adı"
          value={userName}
          data-testid="kullanıcı"
          required
          onChange={(e) => setUserName(e.target.value)}
        />
      </Form.Group>
      <p></p>

      <Form.Group>
        <Form.Label>kullanıcı adı tekrar</Form.Label>
        <Form.Control
          type="text"
          placeholder="tekrar aynı kullanıcı adı"
          value={userNameConfirm}
          data-testid="userNameConfirm"
          required
          onChange={(e) => setUserNameConfirm(e.target.value)}
        />
      </Form.Group>
      <p></p>

      {!isPending && (
        <Button
          variant="primary"
          type="submit"
          data-testid="changeUserNameButton"
        >
          gönder
        </Button>
      )}
      {isPending && <Button variant="primary">lütfen bekleyin...</Button>}
    </Form>
  );
};

export default ChangeUsername;
