import React from "react";
import { useHistory } from "react-router-dom";
import Navbar from "react-bootstrap/Navbar";
import NavbarToggle from "react-bootstrap/NavbarToggle";
import NavbarCollapse from "react-bootstrap/NavbarCollapse";
import { useDispatch } from "react-redux";
import Button from "react-bootstrap/Button";
import Dropdown from "react-bootstrap/Dropdown";
import DropdownButton from "react-bootstrap/DropdownButton";
import { resetState } from "./redux";

const Navigation = ({
  isAuthenticated,
  signout,
  nick,
  checkAuth,
  users,
  isloading,
}) => {
  const history = useHistory();
  const dispatch = useDispatch();

  const signoutHandler = (e) => {
    e.preventDefault();
    signout();

    try {
      history.push("/");
    } catch (error) {
      console.log(error.message);
      alert(error.message);
    }
  };

  const homeHandler = (e) => {
    e.preventDefault();
    history.push("/");
  };

  const createHandler = (e) => {
    e.preventDefault();
    history.push("/createBlog");
  };

  const entryHandler = (e) => {
    e.preventDefault();
    // window.location = "/signin";
    history.push("/signin");
  };

  const signUpHandler = (e) => {
    e.preventDefault();
    // window.location = "/signup";
    history.push("/signup");
  };

  const changeUsername = (e) => {
    e.preventDefault();
    dispatch(resetState());
    history.push("/changeUsername");
    // window.location = "/changeUsername";
  };

  const changePassword = (e) => {
    e.preventDefault();
    dispatch(resetState());
    history.push("/changePassword");
    // window.location = "/changePassword";
  };

  return (
    <Navbar data-testid="navigation">
      <a href="/" className="powderBlueLink" onClick={homeHandler}>
        başak's blog
      </a>
      <NavbarToggle />
      <NavbarCollapse className="justify-content-end">
        {isAuthenticated && !isloading && (
          <>
            <DropdownButton id="dropdown-basic-button" title={nick}>
              <Dropdown.Item onClick={changeUsername}>
                kullanıcı adı değiştir
              </Dropdown.Item>
              <Dropdown.Item onClick={changePassword}>
                şifre değiştir
              </Dropdown.Item>
            </DropdownButton>
            <Button
              onClick={createHandler}
              variant="primary"
              data-testid="newBlog"
            >
              yeni blog
            </Button>
            <Button onClick={signoutHandler}>çıkış</Button>
          </>
        )}
        {!isAuthenticated && !isloading && (
          <>
            <Button variant="warning" onClick={signUpHandler}>
              kayıt
            </Button>
            <Button onClick={entryHandler} data-testid="navigationEntryButton">
              giriş
            </Button>
          </>
        )}
      </NavbarCollapse>
    </Navbar>
  );
};

// const mapStateToProps = (state) => {
//   return {
//     isAuthenticated: state.isAuth.success,
//   };
// };

// const mapDispatchToProps = (dispatch) => {
//   return {
//     signout: () => dispatch(signout()),
//     checkAuth: () => dispatch(checkAuth()),
//   };
// };

export default Navigation;
