import React from "react";
import { Route, Redirect } from "react-router-dom";

// very delicate component, try not change anything.
export const PrivateRoute = ({
  isloading,
  isAuthenticated,
  nick,
  component: Component,
  ...rest
}) => {
  return (
    !isloading && (
      <Route
        {...rest}
        component={(props) =>
          isAuthenticated ? (
            <Component {...props} />
          ) : (
            <Redirect
              to={{
                pathname: "/signin",
                state: { from: props.location },
              }}
            />
          )
        }
      />
    )
  );
};

export default PrivateRoute;

