import axios from "axios";
import { useState, useEffect, useRef } from "react";
import { useSelector, useDispatch } from "react-redux";
import Alert from "react-bootstrap/Alert";
import BlogList from "./BlogList";
import { resetState } from "./redux";

const Home = () => {
  const [data, setData] = useState(null);
  // eslint-disable-next-line
  const [error, setError] = useState(null);
  const [pending, setPending] = useState(null);
  const unmounted = useRef(null);
  // eslint-disable-next-line
  const [show, setShow] = useState(true);
  const message = useSelector((state) => state.auth?.message);
  const dispatch = useDispatch();

  useEffect(() => {
    if (
      message ===
      "şifre sıfırlama emaili gönderildi. bu sayfayı kapatabilirsiniz"
    )
      dispatch(resetState());
  }, [message, dispatch]);

  useEffect(() => {
    unmounted.current = false;
    setPending(true);
    axios
      .get("/api/blogs")
      .then((res) => {
        //res.data[0];
        // console.log(res);
        setData(res.data[0]);
        setPending(false);
      })
      .catch((err) => {
        console.log(err.message);
        setPending(false);
        setError(err);
      });
    return () => {
      unmounted.current = true;
      setPending(false);
      setShow(false);
      dispatch(resetState());
    };
  }, [dispatch]);

  // console.log("blogs ", blogs);
  return (
    <div className="home">
      {pending && <div>yüklüyor...</div>}
      {message && show && <Alert variant="info">{message}</Alert>}
      {data && <BlogList data={data} title="blog listesi!" />}
    </div>
  );
};

export default Home;
