import { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { /*connect */ useDispatch, useSelector } from "react-redux";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Alert from "react-bootstrap/Alert";
import isEmail from "validator/lib/isEmail";
import { signup } from "./redux";
import { resetState } from "./redux";

const Signup = () => {
  const [email, setEmail] = useState("");
  const [userName, setUserName] = useState("");
  const [password, setPassword] = useState("");
  const [emailCheck, setEmailCheck] = useState("");
  const [passwordConfirm, setPasswordConfirm] = useState("");
  const history = useHistory();
  const error = useSelector((state) => state.auth?.error);
  const message = useSelector((state) => state.auth?.message);
  const logged = useSelector((state) => state.auth?.logged);
  const loading = useSelector((state) => state.auth?.loading);
  const dispatch = useDispatch();

  useEffect(() => {
    try {
      if (logged) history.push("/");
    } catch (error) {
      console.log("error.message ", error.message);
    }

    return () => {
      dispatch(resetState());
    };
  }, [history, logged, dispatch]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      if (!isEmail(email)) {
        setEmailCheck(
          "emailinizde bir problem var. sistem geçerli bir email olarak kabul etmiyor"
        );
        return;
      }
      dispatch(signup({ email, password, passwordConfirm, userName }));
      return;
    } catch (error) {
      console.log(error.message);
    }
  };

  return (
    <>
      {emailCheck && <Alert variant="danger">{emailCheck}</Alert>}
      {error && <Alert variant="danger">{error}</Alert>}
      {message === "" || loading || emailCheck || error ? (
        <Form onSubmit={handleSubmit}>
          <Form.Group controlId="formBasicEmail">
            <Form.Label>email</Form.Label>
            <Form.Control
              type="text"
              placeholder="email"
              value={email}
              data-testid="email"
              onChange={(e) => setEmail(e.target.value)}
            />
            <Form.Text className="text-muted"></Form.Text>
          </Form.Group>
          <Form.Group>
            <Form.Label>kullanıcı adı</Form.Label>
            <Form.Control
              type="text"
              placeholder="kullanıcı adı"
              value={userName}
              data-testid="userName"
              onChange={(e) => setUserName(e.target.value)}
            />
            <Form.Text className="text-muted"></Form.Text>
          </Form.Group>
          <Form.Group controlId="formBasicPassword">
            <Form.Label>şifre</Form.Label>
            <Form.Control
              type="password"
              placeholder="şifre"
              value={password}
              data-testid="password"
              autocomplete="off"
              onChange={(e) => setPassword(e.target.value)}
            />
          </Form.Group>
          <Form.Group controlId="formBasicPassword">
            <Form.Label>şifre tekrar</Form.Label>
            <Form.Control
              type="password"
              placeholder="tekrar aynı şifre"
              value={passwordConfirm}
              data-testid="passwordConfirm"
              autocomplete="off"
              onChange={(e) => setPasswordConfirm(e.target.value)}
            />
          </Form.Group>
          <p></p>
          {!loading && (
            <Button variant="primary" type="submit" data-testid="signinButton">
              gönder
            </Button>
          )}
          {loading && <button>bilgiler gönderiliyor...</button>}
        </Form>
      ) : (
        <h3>{message}</h3>
      )}
    </>
  );
};

export default Signup;
