import { useHistory, useParams } from "react-router";
import { useState, useEffect, useRef, useContext } from "react";
import axios from "axios";
import Button from "react-bootstrap/Button";
import { CKEditor } from "@ckeditor/ckeditor5-react";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import { deleteBlog } from "../src/redux";
import { submitComment } from "./redux";
import { deleteComment } from "./redux";
import { getComments } from "./redux";
import Alert from "react-bootstrap/Alert";
import { useDispatch } from "react-redux";
import { /* connect */ useSelector } from "react-redux";
import Form from "react-bootstrap/Form";
import moment from "moment";
import trLocale from "moment/locale/tr";
import { socketContext } from "./SocketIOContext";

moment.updateLocale("tr", trLocale);

const BlogDetails = () => {
  const dispatch = useDispatch();
  const { id } = useParams();
  const history = useHistory();
  // let users = useContext(userContext);
  // console.log("users.current in BlogDetails", users.current);

  // const [usersLocal, setUsersLocal] = useState([]);

  const [blogAuthorIds, setBlogAuthorIds] = useState(null);
  const [blog, setBlog] = useState("");
  const [comment, setComment] = useState("");
  const [images, setImages] = useState([]);
  const [error, setError] = useState(null);
  const [ind, setInd] = useState(null);
  const needToCheckRef = useRef(true);
  const [sameuser, setSameuser] = useState(null);
  const [pending, setPending] = useState(null);
  const [confirmDelete, setConfirmDelete] = useState(false);
  const unmounted = useRef(null);
  const nick = useSelector((state) => state.auth?.userName);
  const [reload, setReload] = useState(false);
  const [show, setShow] = useState(false);

  const [needForCommentsToLoad, setNeedForCommentsToLoad] = useState(true);
  const comments = useSelector((state) => state.auth?.comments);

  // const loading = useSelector((state) => state.auth?.loading);

  const textAreaRef = useRef(null);
  const [singleCommentBySocketIO, setSingleCommentBySocketIO] = useState(null);

  // eslint-disable-next-line
  const [commentRemoved, setCommentRemoved] = useState(null);

  const socket = useContext(socketContext);

  const [users, setUsers] = useState([]);

  // const [message, setMessage] = useState("");
  // const [messages, setMessages] = useState([]);

  useEffect(() => {
    socket.on("connect", () => {
      socket.emit("username", nick);
    });

    socket.on("users", (users) => {
      setUsers(users);
    });

    // socket.on("message", (message) => {
    //   setMessages((messages) => [...messages, message]);
    // });

    socket.on("connected", (user) => {
      // console.log("user 1111111111111111111", user);
      setUsers((users) => [...users, user]);
    });

    socket.on("disconnected", (id) => {
      setUsers((users) => {
        // console.log(typeof users);
        return users.filter((user) => user.id !== id);
      });
    });
  }, [nick, socket]);

  useEffect(() => {
    socket.on("singleCommentBySocketIO", (commentsArrived) => {
      setSingleCommentBySocketIO(commentsArrived.singleCommentBySocketIO);
      setShow(true);
      setReload(true);
    });

    socket.on("commentRemovedBySocketIO", (commentsArrived) => {
      setCommentRemoved(commentsArrived.commentRemovedBySocketIO._id);
      setReload(true);
    });
  }, [nick, socket, reload]);

  useEffect(() => {
    unmounted.current = false;
    setPending(true);
    let config = {
      headers: {
        "Content-Type": "application/x-www-form-urlencoded",
        Accept: "application/json",
      },
      withCredentials: true,
      crossDomain: true,
    };
    axios
      .get("/api/blogs/" + id, config)
      .then((res) => {
        // console.log("res ", res.data);
        setBlog(res.data[0].blog);
        setSameuser(res.data[0].sameUser);
        // setBlogAuthorIds((blogAuthorIds) =>
        //   blogAuthorIds.concat(res.data[0].blog.blogAuthorId)
        // );

        setBlogAuthorIds(res.data[0].blog.blogAuthorId);

        setImages(res.data[0].blog.imageId);
        dispatch(getComments(id));
        setPending(false);
        setReload(false);
        setNeedForCommentsToLoad(false);
      })
      .catch((err) => {
        console.log(err.message);
        setPending(false);
        setError(err);
      });

    return () => {
      setPending(false);
      unmounted.current = true;
    };
  }, [dispatch, id, reload]);

  const handleClickForEdit = () => {
    history.push(`/blogs/${id}/edit`);
  };

  const handleClickForDelete = (e) => {
    e.preventDefault();
    try {
      dispatch(deleteBlog(id));
      history.push("/");
      // window.location = "/";
    } catch (error) {
      console.log(error.message);
    }
  };

  const handleClickForDeleteForComment = (commentId, e) => {
    e.preventDefault();
    try {
      dispatch(deleteComment(commentId));
      // setReload(true);
      setConfirmDelete(false);
      // dispatch(getComments(id));
    } catch (error) {
      console.log(error.message);
    }
  };

  const handleCommentSubmit = (e, levelZero = null) => {
    e.preventDefault();
    try {
      dispatch(submitComment(comment, id, levelZero));
      // socket.emit("send", message);
      // setMessage("");
      setNeedForCommentsToLoad(false);
      dispatch(getComments(id));
      setComment("");
      // setReload(true);
    } catch (error) {
      console.log(error.message);
    }
  };

  const getAuthors = () => {
    // return "reta";
    return (
      blogAuthorIds && blogAuthorIds.map((value) => value.userName).join(", ")
    );
  };

  const checkIfSuperUser = () => {
    let answer = blogAuthorIds && blogAuthorIds.map((obj) => obj.userName);

    let passed;

    if (answer.includes(nick)) {
      passed = true;
    } else {
      passed = false;
    }

    // console.log("^^^^^^^^^^^^^^ ", passed);

    if (passed) {
      needToCheckRef.current = false;
      return true;
    } else return false;
  };

  const checkIfOwner = (commentAuthorName) => {
    let passed;
    // console.log(commentAuthorName === nick);
    if (commentAuthorName === nick) {
      passed = true;
    } else {
      passed = false;
    }
    // console.log("$$$$$$$$$$$$$$$ ", commentAuthorName);
    if (passed) return true;
    else return false;
  };

  const setShowOnlyOnce = (id) => {
    if (ind === id) return true;
    return false;
  };

  const config = {
    toolbar: {
      items: [],
      viewportTopOffset: 0,
    },
  };

  const showUsers = (users) => {
    return (
      users && (
        <div className="col-md-4">
          <h6>sistemde bulunanlar: </h6>
          <ul id="users" style={{ listStyleType: "none" }}>
            {Array.from(users)
              .map((user) => user.name)
              .filter((value, index, self) => self.indexOf(value) === index)
              .map((name, index) => (
                <li key={index}>{name}</li>
              ))}
          </ul>
        </div>
      )
    );
  };

  const renderBlog = () => {
    return (
      <div>
        <h2>{blog.title}</h2>
        <p>written by {getAuthors(id)}</p>
        <div className="App">
          <CKEditor
            config={config}
            editor={ClassicEditor}
            data={blog.body}
            onReady={(editor) => {
              // You can store the "editor" and use when it is needed.
              // console.log("Editor is ready to use!", editor);
              if (editor) editor.isReadOnly = true;
            }}
            // onChange={(event, editor) => {
            //   const data = editor.getData();
            //   console.log({ event, editor, data });
            // }}
            onBlur={(event, editor) => {
              // console.log("Blur.", editor);
            }}
            onFocus={(event, editor) => {
              // console.log("Focus.", editor);
            }}
          />
        </div>
        <p className="forCenteringImages">
          {images &&
            images.map((img) => {
              return (
                <img
                  alt="blog"
                  src={`data:image/jpeg;base64,${img["data"]}`}
                  height="100px"
                  width="100px"
                  key={img._id}
                  className="images"
                />
              );
            })}
        </p>
      </div>
    );
  };

  return (
    <>
      {confirmDelete && (
        <Alert variant="danger">
          <button
            type="button"
            className="btn btn-primary"
            onClick={() => setConfirmDelete(false)}
          >
            vazgeç
          </button>
          <button
            type="button"
            className="btn btn-danger"
            data-bs-dismiss="modal"
            onClick={(e) => handleClickForDelete(e)}
            data-dismiss="modal"
          >
            eminim blog'u silebilirsin!
          </button>
        </Alert>
      )}

      <div className="blog-details">
        {pending && <div>Loading...</div>}
        {error && <div>{error}</div>}
        {renderBlog()}
        {sameuser ? (
          <>
            <Button
              variant="warning"
              className="forMarginForButton"
              onClick={handleClickForEdit}
              data-testid="editButton"
              id="editButton"
            >
              düzenle
            </Button>
            <Button
              variant="danger"
              className="forMarginForButton"
              onClick={() => setConfirmDelete(true)}
            >
              blog'u sil
            </Button>
          </>
        ) : (
          ""
        )}
        <p></p>
        <div className="flash-comments">
          <div className="header">
            <div>
              <img src="./comments.png" alt="comment logo" />
            </div>
            <div className="text">yorum ekleyebilirsin, {nick}</div>
          </div>
          {/* {console.log("commentRemoved", commentRemoved)} */}
          {commentRemoved &&
            comments.filter(
              (comment) => comment._id.toString() !== commentRemoved.toString()
            )}
          {/* {console.log("comments ", comments)} */}
          {comments &&
            comments.length > 0 &&
            comments.map((comment, index) => {
              return (
                <div key={comment._id}>
                  <span>
                    <b>{comment.commentAuthorId.userName}</b> - a -
                  </span>
                  : &nbsp;
                  <span>
                    {moment(comment?.createdAt).format("ddd MMM yyy hh mm ss")}
                  </span>
                  &nbsp; - <span>{comment?.comment}</span> &nbsp; &nbsp;
                  <br />
                  &nbsp;
                  {checkIfSuperUser() && (
                    <Button
                      variant="danger"
                      onClick={(e) => {
                        setConfirmDelete(true);
                        setInd(index);
                      }}
                    >
                      sil
                    </Button>
                  )}
                  {checkIfOwner(comment.commentAuthorId?.userName) &&
                    needToCheckRef.current && (
                      <Button
                        variant="danger"
                        onClick={(e) => {
                          setConfirmDelete(true);
                          setInd(index);
                        }}
                      >
                        sil
                      </Button>
                    )}
                  {confirmDelete && setShowOnlyOnce(index) && (
                    <Alert variant="danger">
                      <span>Yorumu silmek istedinizden emin misiniz?</span>{" "}
                      &nbsp; &nbsp;
                      <button
                        type="button"
                        className="btn btn-primary"
                        onClick={() => setConfirmDelete(false)}
                      >
                        vazgeç
                      </button>
                      &nbsp;
                      <button
                        type="button"
                        className="btn btn-danger"
                        data-bs-dismiss="modal"
                        onClick={(e) =>
                          handleClickForDeleteForComment(comment._id, e)
                        }
                        data-dismiss="modal"
                      >
                        sil
                      </button>
                    </Alert>
                  )}
                </div>
              );
            })}
          {singleCommentBySocketIO &&
            needForCommentsToLoad &&
            singleCommentBySocketIO._id !== commentRemoved?.toString() && (
              <>
                {/* {console.log(
                  "singleCommentBySocketIO ",
                  singleCommentBySocketIO
                )} */}
                <span>
                  <b>{singleCommentBySocketIO.commentAuthorId.userName}</b> - b-
                </span>{" "}
                &nbsp;
                <span>
                  {moment(singleCommentBySocketIO?.createdAt).format(
                    "ddd MMM yyy hh mm ss"
                  )}
                </span>
                &nbsp; - <span>{singleCommentBySocketIO?.comment}</span>
              </>
            )}
          &nbsp; &nbsp;
          <br />
          &nbsp;
          {singleCommentBySocketIO?._id &&
            needForCommentsToLoad &&
            singleCommentBySocketIO._id !== commentRemoved?.toString() &&
            (checkIfSuperUser(blogAuthorIds) ||
              checkIfOwner(
                singleCommentBySocketIO?.commentAuthorId.userName
              )) && (
              <Button
                variant="danger"
                onClick={(e) => {
                  setConfirmDelete(true);
                }}
              >
                sil
              </Button>
            )}
          {singleCommentBySocketIO?._id && confirmDelete && !show && (
            <Alert variant="danger">
              <span>Yorumu silmek istedinizden emin misiniz?</span> &nbsp;
              &nbsp;
              <button
                type="button"
                className="btn btn-primary"
                onClick={() => setConfirmDelete(false)}
              >
                vazgeç
              </button>
              &nbsp;
              <button
                type="button"
                className="btn btn-danger"
                data-bs-dismiss="modal"
                onClick={(e) =>
                  handleClickForDeleteForComment(singleCommentBySocketIO._id, e)
                }
                data-dismiss="modal"
              >
                sil
              </button>
            </Alert>
          )}
          {
            <>
              <div className="comment-form">
                <div className="row">
                  <Form.Control
                    required
                    id="new_comment_text"
                    as="textarea"
                    rows={2}
                    ref={textAreaRef}
                    value={comment}
                    onChange={(e) => {
                      setComment(e.target.value);
                    }}
                  />
                </div>
              </div>
              <div className="row">
                <button
                  type="submit"
                  onClick={(e) => handleCommentSubmit(e)}
                  className="btn btn-primary btn-md btn-block"
                >
                  yorum yaz
                </button>
              </div>
            </>
          }
          <p></p>
          {error && <div>{error}</div>}
        </div>
      </div>

      {users && showUsers(users)}
    </>
  );
};

export default BlogDetails;
