import { useState } from "react";
import axios from "axios";
// import { useHistory } from "react-router";
import Compress from "browser-image-compression";
import Loader from "react-loader-spinner";
import { CKEditor } from "@ckeditor/ckeditor5-react";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import Alert from "react-bootstrap/Alert";

const Create = () => {
  const [title, setTitle] = useState("");
  const [body, setBody] = useState("");
  const [isPending, setIsPending] = useState(false);
  const [selectedFile, setSelectedFile] = useState([]);
  const [error, setError] = useState("");
  const [imaj, setImaj] = useState([]);
  const [isFilePicked, setIsFilePicked] = useState(false);
  const [wordCount, setWordCount] = useState(0);
  // const history = useHistory();
  const MAX_LENGTH = 4;

  const config = {
    toolbar: {
      items: [
        "heading",
        "|",
        "bold",
        "italic",
        "link",
        "outdent",
        "indent",
        "bulletedList",
        "numberedList",
        "insertTable",
        "blockQuote",
        "undo",
        "redo",
      ],
    },
    link: {
      // Automatically add target="_blank" and rel="noopener noreferrer" to all external links.
      addTargetToExternalLinks: true,
    },
  };
  const changeHandler = (event) => {
    setIsFilePicked(true);
    const options = {
      // As the key specify the maximum size
      // Leave blank for infinity
      maxSizeMB: 1,
      // Use webworker for faster compression with
      // the help of threads
      useWebWorker: true,
    };

    if (Array.from(event.target.files).length > MAX_LENGTH) {
      event.preventDefault();
      setError(`bir blog için ${MAX_LENGTH} resimden fazla yükleyemezsiniz`);
      setIsFilePicked(false);
      return;
    }

    //setSelectedFile([...selectedFile, event.target.files]);
    setSelectedFile(event.target.files);

    setImaj([]);
    for (const file of event.target.files) {
      Compress(file, options)
        .then((compressedBlob) => {
          compressedBlob.lastModifiedDate = new Date();

          const convertedBlobFile = new File([compressedBlob], file.name, {
            type: file.type,
            lastModified: Date.now(),
          });
          let r = URL.createObjectURL(convertedBlobFile);

          setImaj((imaj) => imaj.concat(r));
          setError("");
          // one.src = URL.createObjectURL(event.target.files);
          // one.onload = function () {
          //   URL.revokeObjectURL(one.src); // free memory
          // };
          // };
        })
        .catch((e) => {
          // Show the user a toast message or notification that something went wrong while compressing file
          console.log(e.message);
        });
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setIsPending(true);

    const formData = new FormData();

    for (const file of selectedFile) {
      formData.append("files", file);
    }
    //formData.append("files", selectedFile); //this is wrong. the right way is the for loop above.
    formData.append("body", body);
    formData.append("title", title);

    const config = {
      headers: {
        "Content-Type": "application/x-www-form-urlencoded",
        Accept: "application/json",
      },
      withCredentials: true,
      crossDomain: true,
    };

    axios
      .post("/api/blogs", formData, config)
      .then((result) => {
        setIsPending(false);
        if (result.data.too_many_pictures_at_once) {
          setError("bir kerede 4 resim yükleyebilirsiniz");
          return;
        }
        if (result.data.title_too_short_or_long) {
          setError("başlık 1 karakter ile 100 karakter arasında olmalı");
          return;
        }

        if (result.data.body_too_short_or_long) {
          setError("blog 1 karakter ile 10000 karakter arasında olmalı");
          return;
        } else {
          // history.push("/");
          window.location = "/";
        }
      })
      .catch((error) => {
        console.error("Error:", error);
      });
  };
  return (
    <div className="create">
      {error && <Alert variant="danger">{error}</Alert>}
      <h2 data-testid="newBlogH2">yeni blog ekle</h2>
      <form action="">
        <label htmlFor="">blog başlığı:</label>
        <input
          type="text"
          required
          value={title}
          data-testid="titleInput"
          onChange={(e) => setTitle(e.target.value)}
        />

        <label htmlFor="">blog:</label>
        <div className="App">
          <CKEditor
            editor={ClassicEditor}
            data={body}
            config={config}
            onReady={(editor) => {
              // You can store the "editor" and use when it is needed.
              // console.log("Editor is ready to use!", editor);
            }}
            onChange={(event, editor) => {
              const data = editor.getData();
              setBody(data);
              // console.log({ event, editor, data });
              setWordCount(data.length);
            }}
            onBlur={(event, editor) => {
              // console.log("Blur.", editor);
            }}
            onFocus={(event, editor) => {
              // console.log("Focus.", editor);
            }}
          />
        </div>
        <div id="word-count">
          {10000 - wordCount} karater yazma hakkınız kaldı
        </div>
        <p></p>
        <Alert variant="info">
          bu ekranda bir kerede en fazla 4 resim yükleyebilirsiniz
        </Alert>
        <p></p>
        <input
          type="file"
          name="files"
          id="files"
          multiple="muliple"
          accept="image/*"
          onClick={(e) => (e.target.value = null)}
          onChange={changeHandler}
        />
        <p></p>
        {imaj &&
          imaj.map((im, index) => (
            <img
              id="one"
              src={im}
              key={index}
              alt="upload"
              width="100"
              height="100"
              className="images"
            />
          ))}
        {isFilePicked && (
          <Loader
            type="Puff"
            color="#00BFFF"
            height={100}
            width={100}
            timeout={3000} //3 secs
          />
        )}
        <p></p>
        {!isPending && (
          <button data-testid="blogAddButton" onClick={handleSubmit}>
            blog ekle
          </button>
        )}
        {isPending && <button>blog ekliyor...</button>}
      </form>
      <p></p>
      {error && <Alert variant="danger">{error}</Alert>}
    </div>
  );
};

export default Create;
