import { useState, useEffect } from "react";
import { useHistory, useLocation } from "react-router-dom";
import { /* connect */ useDispatch, useSelector } from "react-redux";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Alert from "react-bootstrap/Alert";
import { postUser /*checkAuth*/ } from "./redux";
import { resetState } from "./redux";

const Signin = () => {
  const { state } = useLocation();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const history = useHistory();
  const error = useSelector((state) => state.auth?.error);
  // const loading = useSelector((state) => state.auth?.loading);
  const logged = useSelector((state) => state.auth?.logged);
  const dispatch = useDispatch();
  // signing in does not work with dispatch hook (which is from react-redux package).
  // console.log("state ", state);

  useEffect(() => {
    try {
      // dispatch(checkAuth());
      // console.log("error", error);
      const link = (state && state.from) || "/";

      if (logged) history.push(link);
    } catch (error) {
      console.log("error.message ", error.message);
    }

    return () => {
      dispatch(resetState());
    };
  }, [logged, history, state, dispatch]);

  const handleForgotPassword = (e) => {
    e.preventDefault();
    history.push("/forgotPassword");
  };
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      dispatch(postUser(email, password));
      // const link = (state && state.from) || "/";
      history.push("/signin");
      // return;
    } catch (error) {
      console.log(error.message);
    }
  };

  return (
    <Form onSubmit={handleSubmit}>
      {error && <Alert variant="danger">{error}</Alert>}
      <Form.Group controlId="formBasicEmail">
        <Form.Label>email</Form.Label>
        <Form.Control
          type="email"
          placeholder="email"
          data-testid="email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
        />
        <Form.Text className="text-muted"></Form.Text>
      </Form.Group>

      <Form.Group controlId="formBasicPassword">
        <Form.Label>şifre</Form.Label>
        <Form.Control
          type="password"
          placeholder="Password"
          value={password}
          data-testid="password"
          autocomplete="current-password"
          onChange={(e) => setPassword(e.target.value)}
        />
      </Form.Group>
      <p></p>
      <Button variant="primary" type="submit" data-testid="signinButton">
        giriş yap
      </Button>
      <Button
        variant="secondary"
        className="float-end"
        type="button"
        data-testid="forgetPasswordButton"
        onClick={handleForgotPassword}
      >
        şifremi unuttum
      </Button>
    </Form>
  );
};

// Recommended way is to use hooks from react-redux library instead of
// mapStateToProps && mapDispatchToProps methods.
// const mapStateToProps = (state) => {
//   return {
//     userData: state,
//   };
// };

// const mapDispatchToProps = (dispatch) => {
//   return {
//     postUser: (email, password) => dispatch(postUser(email, password)),
//   };
// };

export default Signin;
