import { Link } from "react-router-dom";

const {
  POST_USER_REQUEST,
  POST_USER_SUCCESS,
  POST_USER_FAILURE,
  POST_AUTH_SUCCESS,
  POST_AUTH_FAILURE,
  POST_AUTH_REQUEST,
  POST_SIGNOUT_REQUEST,
  POST_SIGNOUT_FAILURE,
  POST_SIGNOUT_SUCCESS,
  SIGN_UP_FAILURE,
  SIGN_UP_REQUEST,
  SIGN_UP_SUCCESS,
  DELETE_FAILURE,
  DELETE_REQUEST,
  DELETE_SUCCESS,
  FORGOT_PASSWORD_REQUEST,
  FORGOT_PASSWORD_SUCCESS,
  FORGOT_PASSWORD_FAILURE,
  RESET_PASSWORD_REQUEST,
  RESET_PASSWORD_SUCCESS,
  RESET_PASSWORD_FAILURE,
  CHANGE_USERNAME_REQUEST,
  CHANGE_USERNAME_SUCCESS,
  CHANGE_USERNAME_FAILURE,
  CHANGE_PASSWORD_REQUEST,
  CHANGE_PASSWORD_SUCCESS,
  CHANGE_PASSWORD_FAILURE,
  CONFIRM_ACCOUNT_REQUEST,
  CONFIRM_ACCOUNT_SUCCESS,
  CONFIRM_ACCOUNT_FAILURE,
  SUBMIT_COMMENT_REQUEST,
  SUBMIT_COMMENT_SUCCESS,
  SUBMIT_COMMENT_FAILURE,
  GET_COMMENTS_REQUEST,
  GET_COMMENTS_SUCCESS,
  GET_COMMENTS_FAILURE,
  DELETE_COMMENT_REQUEST,
  DELETE_COMMENT_SUCCESS,
  DELETE_COMMENT_FAILURE,
  RESET_STATE,
} = require("./authTypes");

const initialState = {
  loading: false,
  error: "",
  logged: false,
  userName: "",
  statusCode: "",
  message: "",
  changedName: "",
  authorNames: [],
  comments: [],
};

export const authReducer = (state = initialState, action) => {
  switch (action.type) {
    case POST_USER_REQUEST:
      return {
        ...state,
        loading: true,
        logged: false,
        error: "",
      };
    case POST_USER_SUCCESS:
      return {
        ...state,
        loading: false,
        logged: action.payload.logged,
        userName: action.payload.userName,
        error: "",
      };

    case POST_USER_FAILURE:
      // console.log("aaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaa", action.payload);
      return {
        ...state,
        loading: false,
        logged: false,
        error: action.payload,
      };
    case POST_AUTH_REQUEST:
      return {
        ...state,
        loading: true,
        logged: false,
      };

    case POST_AUTH_SUCCESS:
      // console.log("action.payload ", action.payload);
      return {
        ...state,
        loading: false,
        logged: action.payload.logged,
        userName: action.payload.userName,
      };

    case POST_AUTH_FAILURE:
      return {
        ...state,
        loading: false,
        error: action.payload.error,
        logged: false,
      };
    case SIGN_UP_REQUEST:
      return {
        ...state,
        loading: true,
      };

    case SIGN_UP_SUCCESS:
      // console.log("action.payload ", action.payload);
      return {
        ...state,
        loading: false,
        logged: action.payload.logged,
        error: "",
        message:
          action.payload.message === "pending"
            ? "kayıt işleminzi tamamlamak için lütfen email kutunuzu kontrol edin"
            : "",
      };

    case SIGN_UP_FAILURE:
      return {
        ...state,
        loading: false,
        error: action.payload,
        logged: false,
      };
    case POST_SIGNOUT_REQUEST:
      return {
        ...state,
        loading: true,
      };

    case POST_SIGNOUT_SUCCESS:
      return {
        ...state,
        loading: false,
        message: "",
        logged: !action.payload.loggedOut,
      };

    case POST_SIGNOUT_FAILURE:
      return {
        ...state,
        loading: false,
        error: action.payload.error,
      };
    case DELETE_REQUEST:
      return {
        ...state,
        loading: true,
      };

    case DELETE_SUCCESS:
      // console.log("action ", action);
      return {
        ...state,
        loading: false,
        error: action.payload,
      };

    case DELETE_FAILURE:
      // console.log("action ", action);
      return {
        ...state,
        loading: false,
        error: action.payload.error,
      };

    case FORGOT_PASSWORD_REQUEST:
      return {
        ...state,
        loading: true,
      };
    case FORGOT_PASSWORD_SUCCESS:
      return {
        ...state,
        loading: false,
        message:
          action.payload.message !== ""
            ? "şifre sıfırlama emaili gönderildi. bu sayfayı kapatabilirsiniz"
            : "",
      };

    case FORGOT_PASSWORD_FAILURE:
      // console.log("!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!", action.payload);
      return {
        ...state,
        loading: false,
        error: action.payload,
      };
    case RESET_PASSWORD_REQUEST:
      return {
        ...state,
        loading: true,
      };
    case RESET_PASSWORD_SUCCESS:
      return {
        ...state,
        loading: false,
        logged: action.payload.logged,
        error: "",
        message:
          action.payload.logged === true
            ? "şifre yenileme işlemi başarılı"
            : "",
      };

    case RESET_PASSWORD_FAILURE:
      // console.log("!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!", action.payload);
      return {
        ...state,
        loading: false,
        error: action.payload,
        statusCode:
          action.statusCode === 401 ? (
            <Link to="/forgotPassword">
              şifremi unuttum sayfasını için geri dön
            </Link>
          ) : (
            ""
          ),
      };
    case CHANGE_USERNAME_REQUEST:
      return {
        ...state,
        loading: true,
      };
    case CHANGE_USERNAME_SUCCESS:
      return {
        ...state,
        loading: false,
        error: "",
        userName: action.payload.userName,
        message:
          action.payload.message === "username succseesfully changed"
            ? "kullanıcı adı başarıyla değiltirlidi"
            : "",
      };

    case CHANGE_USERNAME_FAILURE:
      // console.log("!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!", action.payload);
      return {
        ...state,
        loading: false,
        error: action.payload,
      };

    case CHANGE_PASSWORD_REQUEST:
      return {
        ...state,
        loading: true,
      };
    case CHANGE_PASSWORD_SUCCESS:
      return {
        ...state,
        loading: false,
        error: "",
        message:
          action.payload.message === "password succseesfully changed"
            ? "şifre başarıyla değiltirlidi"
            : "",
      };

    case CHANGE_PASSWORD_FAILURE:
      // console.log("!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!", action.payload);
      return {
        ...state,
        loading: false,
        error: action.payload,
      };

    case CONFIRM_ACCOUNT_REQUEST:
      return {
        ...state,
        logged: false,
      };
    case CONFIRM_ACCOUNT_SUCCESS:
      return {
        ...state,
        logged: action.payload.logged,
        userName: action.payload.userName,
        error: "",
        message:
          action.payload.message === "success"
            ? "kayıt işleminiz tamamlandı. hoşgeldiniz!"
            : "",
      };
    case CONFIRM_ACCOUNT_FAILURE:
      // console.log("!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!", action.payload);
      return {
        ...state,
        logged: false,
        error: action.payload,
      };

    case SUBMIT_COMMENT_REQUEST:
      return {
        ...state,
        // loading: true,
      };
    case SUBMIT_COMMENT_SUCCESS:
      return {
        ...state,
        // logged: action.payload.logged,
        // userName: action.payload.userName,
        // error: "",
        // loading: false,
        message: action.payload === "" ? "yorumunuz kaydedildi" : "",
        // comments: action.payload.comments,
      };
    case SUBMIT_COMMENT_FAILURE:
      // console.log("!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!", action.payload);
      return {
        ...state,
        // loading: false,
        error: action.payload,
      };

    case GET_COMMENTS_REQUEST:
      return {
        ...state,
        // loading: true,
      };
    case GET_COMMENTS_SUCCESS:
      // console.log("!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!", action.payload);
      return {
        ...state,
        // logged: action.payload.logged,
        // userName: action.payload.userName,
        // error: "",
        // loading: false,
        comments: action.payload,
      };
    case GET_COMMENTS_FAILURE:
      // console.log("!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!", action.payload);
      return {
        ...state,
        // loading: false,
        comments: [],
        error: action.payload,
      };

    case DELETE_COMMENT_REQUEST:
      return {
        ...state,
        // loading: true,
      };
    case DELETE_COMMENT_SUCCESS:
      // console.log("!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!", action.payload);
      return {
        ...state,
        // logged: action.payload.logged,
        // userName: action.payload.userName,
        // error: "",
        // loading: false,
        message: action.payload.success === true ? "yorum silindi" : "",
      };
    case DELETE_COMMENT_FAILURE:
      // console.log("!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!", action.payload);
      return {
        ...state,
        // loading: false,
        error: action.payload,
      };
    case RESET_STATE:
      return {
        ...state,
        error: "",
        message: "",
        // action.payload?.message === "username succseesfully changed"
        //   ? "kullanıcı adı başarıyla değiltirlidi"
        //   : "",
      };
    default:
      return state;
  }
};
