import { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Alert from "react-bootstrap/Alert";
import { resetPassword } from "../src/redux";

const ResetPassword = () => {
  const { token } = useParams();
  const [isPending, setIsPending] = useState(false);
  const [password, setPassword] = useState("");
  const [postError, setPostError] = useState("");
  const [passwordConfirm, setPasswordConfirm] = useState("");
  const history = useHistory();
  const error = useSelector((state) => state.auth?.error);
  const logged = useSelector((state) => state.auth?.logged);
  const link = useSelector((state) => state.auth?.statusCode);
  const message = useSelector((state) => state.auth?.message);
  const dispatch = useDispatch();

  useEffect(() => {
    try {
      setIsPending(false);
      if (logged) history.push("/");
    } catch (error) {
      console.log("error.message ", error.message);
    }
  }, [logged, history, isPending]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsPending(true);

    try {
      dispatch(resetPassword({ password, passwordConfirm, token }));
      return;
    } catch (error) {
      setPostError(error.message);
      console.log(error.message);
      setIsPending(false);
    }
  };

  return (
    <Form onSubmit={handleSubmit}>
      {error && <Alert variant="danger">{error}</Alert>}
      {link && <Alert variant="danger">{link}</Alert>}
      {postError && !message && <Alert variant="danger">{postError}</Alert>}
      <Form.Group controlId="formBasicPassword">
        <Form.Label>şifre</Form.Label>
        <Form.Control
          type="password"
          placeholder="şifre"
          value={password}
          data-testid="password"
          required
          onChange={(e) => setPassword(e.target.value)}
        />
      </Form.Group>
      <Form.Group controlId="formBasicPassword">
        <Form.Label>şifre tekrar</Form.Label>
        <Form.Control
          type="password"
          placeholder="tekrar aynı şifre"
          value={passwordConfirm}
          data-testid="passwordConfirm"
          required
          onChange={(e) => setPasswordConfirm(e.target.value)}
        />
      </Form.Group>
      <p></p>

      {!isPending && (
        <Button variant="primary" type="submit" data-testid="signinButton">
          gönder
        </Button>
      )}
      {isPending && <Button variant="primary">lütfen bekleyin...</Button>}
    </Form>
  );
};

export default ResetPassword;
